* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e1d6e8;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;

  padding: 8px;
  max-width: 320px;
  margin-top: 15px;
  margin-bottom: 16px;
  margin-left: 75px;

  font-size: 20px;
  font-weight: 600;
  
  border-radius: 15px;
  border: 1px solid;
}

.country-list,
.country-info {
  list-style: none;
  margin-left: 75px;
}

.country-list li {
  display: flex;
  padding-bottom: 10px;

  font-size: 20px;
  font-weight: 600;

  align-items: center;
}

.country-list img,
.country-info img {
  margin-right: 10px;

  border-radius: 10px;
}

.country-info h1 {
  display: flex;
  align-items: center;
}

.country-info span {
  font-weight: 450;
}